
<template>
  <div class="hello">
    <h2>Essential Links</h2>

    <ul id="ul">
        <li>1</li>
        <li>2</li>
        <li>3</li>
        <li>4</li>
    </ul>

		<footer-bar></footer-bar>
  </div>
</template>

<script>

import '../utils/sortable.js'
import '../utils/abc.js'

import footerBar from "@/views/Layout/footerBar"



export default {
  name: 'Demo',

	components: {
		footerBar
	},

  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted() {
    const el = document.querySelector('#ul');
    Sortable.create(el, {animation: 98});

    //console.log(Abc);

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  height: 48px;
  line-height: 48px;
  background-color: #ccc;
  margin: 10px 0;
}
a {
  color: #42b983;
}
</style>



